
.contact-wrp {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	padding: 10%;
	.emailbox {
		background: $theme;
		display: flex;
		@extend .flex-vhcenter;
		color: black !important;
		width: 325px;
		height: 225px;
		font-size: .6rem;
		text-transform: uppercase;
		text-align: center;
		h3 {
			font-size: 16px;
			color: $black;
			margin-bottom: 0px;
		}
		i {
			@extend .flex-vhcenter;
			font-size: 2em;
			border: 2px solid black;
			border-radius: 50%;
			width: 60px;
			height: 60px;
			text-align: center;
		}
		span {
			display: block;
			margin: 7px 0;
			font-weight: 700;
			font-size: .9rem;
		}
		&:hover {
			opacity: .8;
		}
	}
	ul {
		list-style: none;
		padding: 0;
		li {
			display: inline-block;
			font-size: 1.5rem;
			a {
				color: $gray;
			}
			a:hover {
				color: $theme;
			}
		}
	}
}