.techne-wrp {
	width: 100%;
	td {
		width: 50%;
		padding: 5%;
		border-top: 2px solid;
		border-right: 2px solid;
		border-color: $theme !important;
		h3 {
			margin-top: 0px;
			font-weight: 500;
		}
	}
	td:nth-child(1) {
		border-left: 2px solid;
		color: $black;
	}

}

.pasion-txt-wrp {
	max-width: 90%;
	color: $gray;
}

.pasion-txt-wrp p {
	margin-bottom: 0px;
}

.pasion-txt-wrp i {
	font-size: 15px;
	color: #FFC107;
}

.pasion-txt-wrp a {
	font-style: italic;
}

.pasion-txt-wrp h3 {
	width: 100%;
	padding-bottom: 12px;
	color: $gray;
}

td.stats-wrp {
	h1 {
		color: $gray;
	}
}

.chart {
	position: relative;
	width: 100%;
	height: 20px;
	margin-bottom: 15px;
	display: flex;
	align-items: center;
	.percent {
		position: absolute;
		display: flex;
		align-items: center;
		height: 1px;
		font-family: $head-font;
		text-transform: uppercase;
		//font-weight: 700;	
		background: rgba($black, .75);
		i {
			margin: 0 10px;
		}
		&:after {
			content: attr(data-sk-val);
			position: absolute;
			left: 101%;
		}
	}
}

.tech-footer {
	padding: 5%;
	border: 2px solid $theme;
	@extend .text-capitalize;
}

@media only screen and (max-width: 768px) {
	.techne-wrp {
		td {
			width: 100%;
			display: block;
		}
		td:nth-child(2) {
			display: none;
		}
	}
}
