body.en .txt-es {
	display: none !important;
}

body.es .txt-en {
	display: none !important;
}

.langswitch.active {
	color: $theme !important;
}

html,
body {
	font-size: 14px;
}

body {
	font-family: $body-font;
	font-weight: 400;
	letter-spacing: 1px;
	line-height: 1.7;
	color: #333333;
	padding: 0;
	margin: 0;
	background: $white;
	overflow: hidden;
}

h1,
h2,
h3 {
	color: $theme;
}

h1 {
	font-family: $head-font;
	line-height: 1;
}

h2,
h3 {
	font-family: $head-font;
	text-transform: uppercase;
	line-height: 1.2;
}

h2 {
	font-size: 1.5em;
}

h2 span {
	display: block;
	font-size: 1rem;
	padding-top: 0;
}

h3 {
	font-size: 1em;
}

section:hover,
section:focus {
	outline: 0 !important;
}

section {
	position: relative;
}

a {
	transition: $transition;
	text-decoration: none;
	color: $theme;
}

a:hover {
	color: darken($theme, 10%);
	text-decoration: none;
}

[class*="scroll-"] {
	@extend .backfix;
}

a,
button,
a:focus,
button:focus {
	outline: none !important;
}

hr.main-line {
	border: none;
	height: $line-weight;
	margin: $line-air;
	background: $theme;
}

main.st-main {
	position: relative;
	background: $white;
	min-height: 100vh;
}
