.plx-header {
	position: relative;
	width: 100%;
	height: 100vh;
	background: #FAFAFA;
	overflow: hidden;
	.header-bg {
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0%;
		top: 0;
		background: url('../img/bg-about.jpg') no-repeat center;
		background-size: cover;
		filter: grayscale(100%);
		&:after {
			content: '';
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			position: absolute;
			opacity: .9;
			background: $white;
		}
		&.bg-touch {
			background-image: url('../img/bg-touch.jpg');
		}
	}
	.title {
		display: flex;
		flex-direction: column;
		justify-content: center;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		padding: $gpadding;
		h1 {
			max-width: $titlewidth;
			font-size: $titlesize;
			line-height: 1.2em;
			color: $titlecolor;
			text-transform: uppercase;
			strong {
				font-weight: 300;
			}
			.sub-headline {
				display: block;
				max-width: 450px;
				font-family: $body-font;
				font-size: 1rem;
				font-weight: 400;
				line-height: 1.7;
				text-transform: none;
				color: $titlecolor;
				@extend .bef-line;
			}
		}
	}
	.to-anchor {
		position: absolute;
		bottom: 15px;
		left: 50%;
		transform: translateX(-50%);
		font-size: 2.5em;
		color: $theme;
	}
}
