.process-flow {
	display: flex;
	.process-flow_item {
		width: 20%;
		padding: 0 10px;
		font-weight: 700;
		div {
			text-transform: none;
			font-weight: 400;
			@extend .bef-line;
			&:before {
				margin: 1em 0;
			}
		}
	}
}

@media only screen and (max-width: 768px) {
	.process-flow {
		flex-direction: column;
		.process-flow_item {
			width: 100%;
			padding-bottom: 30px;
		}
		.process-flow_item:nth-child(5) {
			padding-bottom: 0px !important;
		}
	}
}
