.wellcome-language {
  @extend .flex-vhcenter;
  flex-direction: row;
  position: fixed;
  width: 100%;
  height: 100vh;
  font-size: 14px;
  background: white;
  z-index: 777777;
  a {
    @extend .text-uppercase;
    @extend .langswitch;
    color: $black !important;
    &:before {
      margin: 0 8px !important;
    }
    &:first-child:before {
      display: none;
    }
    &:hover {
      color: $theme !important;
    }
  }
}