#portfolio-headline {
	position: sticky;
	top: -1px;
	z-index: 10;
	min-height: 80px;
	h3 {
		font-weight: 500;
	}
}

.pt-tuux {
	display: flex;
	flex-wrap: wrap;
}

.portfolio-item {
	position: relative;
	width: 20vw;
	height: 25vw;
	flex-grow: 1;
	&.col-2x {
		width: 40vw;
		&:after {
			content: 'FEATURED PROJECT';
			position: absolute;
			padding: 5px 10px;
			top: 0;
			right: 0;
			font-size: 9px;
			font-weight: 700;
			color: $gray;
			background: $theme;
		}
	}
	a.pt-image {
		display: block;
		width: 100%;
		height: 100%;
		background-color: $theme;
		position: relative;
		background: no-repeat center center;
		background-size: cover;
		overflow: hidden;
		.pt-overinfo {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			padding: 50px;
			font-size: .8rem;
			text-transform: uppercase;
			background: rgba($black, .9);
			color: $theme;
			opacity: 0;
			transition: $transition;
			h2 {
				color: $theme;
				font-weight: 700;
				margin: 0;
			}
			.partner {
				text-transform: capitalize;
			}
		}
		.pt-overinfo:hover {
			opacity: .95;
		}
	}
}

.portfolio-item.even {
	a.pt-image {
		float: right;
	}
}

.pt-inn {
	.row {
		padding: 2.5% 0;
	}
	h2 {
		font-size: 2rem;
		color: $gray;
		font-family: $head-font;
		font-weight: 700;
		margin: 0 !important;
	}
	h3 {
		font-size: 1.2em;
		font-weight: 500;
		margin: 2px 0 0 0 !important;
	}
	.btn-block {
		margin: 15px auto 75px auto;
	}
	p {
		margin: 0px;
	}
}

.tasks {
	position: relative;
	text-align: center;
	h3 {
		text-transform: uppercase;
		padding: 5px 0px;
		margin: 0;
	}
	ul {
		margin: 0;
		padding: 0;
		li {
			margin: 3px 3px 3px 0px;
			list-style: none;
			display: inline-block;
			span,
			a {
				display: inline-block;
				font-size: 11px;
				padding: 5px 0px;
				list-style: none;
				color: $gray;
				text-transform: uppercase;
				&:before {
					content: '\f02b';
					padding: 0 10px;
					font-family: fontawesome;
				}
			}
			a:hover {
				opacity: .75;
			}
			&.date span:before {
				content: '\f073';
			}
			&.partner span:before {
				content: '\f007';
			}
		}
	}
}

.with {
	text-align: center;
	h3 {
		display: inline-block;
		text-transform: uppercase;
		padding: 5px 0px;
		font-size: 1rem;
		margin: 0 !important;
		color: $gray;
		vertical-align: middle;
	}
	span {
		font-size: 11px;
		text-transform: uppercase;
		&:before {
			content: '\f02b';
			padding: 0 5px;
			font-family: fontawesome;
		}
	}
	span.date:before {
		content: '\f073';
	}
	span.partner:before {
		content: '\f007';
	}
}


/* VBOX OVERRIDES */

.vbox-container {
	width: 100%;
	max-width: none;
	padding: 0;
	img {
		margin: 0 auto;
		display: block;
	}
}

.vbox-inline {
	position: relative;
	overflow-x: hidden;
}

.vbox-overlay {
	background: $white;
	overflow: hidden;
}

.vbox-container img {
	max-width: 700px;
}


body.vbox-open .vbox-overlay {
	opacity: 1 !important;
	transform: translateY(0%);
	animation-name: appear;
	animation-duration: .6s;
}


/* The animation code */

@keyframes appear {
	from {
		transform: translateY(100%);
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
	to {
		transform: translateY(0%);
	}
}

@media only screen and (max-width: 768px) {
	.portfolio-item {
		width: 100vw;
		height: 200px;
	}
	body.vbox-open .vbox-content {
		padding: 0 20px;
	}
}