a {
	color: $theme;
	transition: $transition;
	text-decoration: none;
}

a:hover {
	color: darken($theme, 15%);
	text-decoration: none;
}

@mixin mxn-btn($color, $size, $radius, $border) {
	display: inline-block;
	margin: 0px;
	padding: .5em 1.2em;
	font-size: $size;
	font-weight: normal;
	text-decoration: none;
	text-align: center;
	color: lighten($color, 95%);
	background-color: $color;
	border-radius: $radius;
	border: $border solid $color;
	cursor: pointer !important;
	outline: 0;
	transition: $transition;
	&:focus,
	&:hover {
		opacity: .75;
	}
}

button,
input[type="button"],
input[type="reset"],
input[type="submit"],
.btn-main {
	@include mxn-btn($theme, 1em, 4px, 2px);
}

.btn-ghost {
	@include mxn-btn($theme, 1em, 4px, 2px);
	background: transparent;
	color: $theme; //same as mixin variable
}

.btn-link {
	@include mxn-btn($theme, 1em, 0px, 0px);
	background: transparent;
	color: $theme; //same as mixin variable
}

.btn-block {
	display: table;
	width: auto !important;
	margin: 0 auto;
	padding: 15px 30px;
	text-align: center;
	text-transform: uppercase;
	font-weight: 500;
	background: $theme;
	font-size: .8rem;
	color: $gray;
	&:hover {
		opacity: .8;
	}
}

.btn-next {
	position: fixed;
	top: 50vh;
	right: 0;
	transform: translateX( calc(100% - 40px));
	font-size: 12px;
	padding: 10px 0px;
	color: $theme;
	background-color: black;
	transition: transform .6s ease, padding .6s ease;
	text-transform: uppercase;
	z-index: 10;
	&:before {
		content: '';
		@extend .flex-vhcenter;
		position: absolute;
		top: 0;
		left: 0px;
		padding-left: 5px;
		border: 4px solid darken($gray, 2.5%);
		width: 40px;
		height: 100%;
		background-color: darken($gray, 2.5%);
		background-image: url(../img/envelope.svg);
		background-size: 60%;
		background-repeat: no-repeat;
		background-position: center 48%;
		font-size: 18px;
		color: $white;
	}
	&:hover {
		transform: translate(0%);
		color: $theme;
		opacity: 1 !important;
		padding: 10px 20px 10px 50px;
	}
}


.fancylink {
	display: inline-block;
	text-decoration: none;
	text-transform: uppercase;
	color: $gray;
	font-weight: 700;
	&:before {
		content: "\f105";
		display: inline-block;
		font-family: fontawesome;
		width: 15px;
		color: $theme;
		transition: $transition;
	}
	&:hover:before {
		width: 30px;
	}
	&:hover {
		color: $theme;
	}
}

@media only screen and (max-width: 768px) {
	.btn-next {
		display: none;
	}
}
